@media screen and (max-width: 992px) {
  body.lock {
    overflow: hidden;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 41px;
    height: 41px;
    z-index: 3;
    background-color: #E0EEF9;
  }
  .header__burger:before, .header__burger:after {
    content: '';
    background-color: #464646;
    position: absolute;
    width: 60%;
    height: 4px;
    left: 50%;
    transform: translate(-50%, 0);
    transition: all 0.3s ease 0s;
  }
  .header__burger:before {
    top: 11px;
  }
  .header__burger:after {
    bottom: 10px;
  }
  .header__burger span {
    position: absolute;
    background-color: #464646;
    left: 50%;
    transform: translate(-50%, 0);
    width: 60%;
    height: 4px;
    top: 19px;
    transition: all 0.3s ease 0s;
  }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 19px;
    left: 8px;
  }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 17px;
    left: 8px;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__mnu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: all 0.3s ease 0s;
    overflow: auto;
  }
  .header__mnu .header__list {
    list-style-type: none;
    display: block;
    margin-top: 90px;
    padding: 0;
  }
  .header__mnu .header__list li {
    margin-bottom: 40px;
  }
  .header__mnu.active {
    top: 0;
    z-index: 1;
  }
  .swiper-pagination.active {
    z-index: 0;
  }
}

